<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    
    <button class="btn btn-outline-info" @click="openModel(true)"></button>
    
    <table class="table table-hover mt-4">
      <thead>
        <tr>
          <th scope="col"></th>
          <th scope="col"></th>
          <th scope="col"> % </th>
          <th scope="col"></th>
          <th scope="col"></th>
          <th scope="col"> / </th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, key) in coupons" :key="item.id">
          <th scope="row">{{ item.title }}</th>
          <td>{{ item.code }}</td>
          <td>{{ item.percent }}</td>
          <td>{{ item.due_date | timestamp }}</td>
          <td>
            <b class="text-success" v-if="item.is_enabled"></b>
            <b class="text-danger" v-else></b>
          </td>
          <td>
            <button class="btn btn-outline-primary mr-1" @click="openModel(false, item)"></button>
            <button class="btn btn-outline-secondary " @click="deletData(item.id)"></button>
          </td>
        </tr>
      </tbody>
    </table>
    <div class="modal fade" id="modal-add" tabindex="-1" role="dialog" aria-hidden="true">
      <div class="modal-dialog modal-dialog-centered modal-lg" role="document">
        <form class="modal-content" @submit.prevent="updateData(true)">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLongTitle"></h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-md-12 mt-2">
                <label></label>
                <input type="text" class="form-control" placeholder="" v-model="tempData.title">
              </div>
              <div class="col-md-6 mt-2">
                <label>(%)</label>
                  <input type="text" class="form-control" placeholder=" 80 " v-model="tempData.percent">
              </div>
              <div class="col-md-6 mt-2">
                <div class="form-group">
                  <label></label>
                  <input type="date" class="form-control" name="end-date" 
                  :class="{'is-invalid': errors.has('end-date')}"
                  v-model="due_date" v-validate="'required'">
                </div>
              </div>
              <div class="col-md-12 mt-2">
                <div class="form-group">
                  <label for="coupon-code"></label>
                  <input type="text" class="form-control" name="coupon-code" id="coupon-code"
                  :class="{'is-invalid': errors.has('coupon-code')}"
                  placeholder="" v-model="tempData.code" v-validate="'required'">
                </div>
              </div>
              <div class="col-md-12 mt-3">
                <label>
                  <input type="checkbox" value="0" v-model="tempData.is_enabled"> /
                </label>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal"></button> 
            <button type="submit" class="btn btn-primary"></button>
          </div>
        </form>
      </div>
    </div>
    <pagination :page-data="pagination" @changepage="getCoupon"></pagination>
  </div>
</template>

<script>
import $ from 'jquery';
export default {
  data() {
    return {
      coupons:[],
      pagination: {},
      tempData: {
        title: '',
        percent: 100,
      },
      status: {
        fileUploading: false,
      },
      due_date: '',
      isLoading: false,
      isNew: false,
    }
  },
  watch: {
    due_date() {
      const vm = this;
      const timestamp = Math.floor(new Date(vm.due_date) / 1000);
      vm.tempData.due_date = timestamp;
    },
  },
  methods: {
    getCoupon(num = 1){
      let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupons?page=${num}`;
      const vm = this;
      vm.isLoading= true;
      this.$http.get(api).then((response) => {
        vm.coupons = response.data.coupons;
        vm.isLoading= false;
        vm.pagination = response.data.pagination;
        console.log(response)
      })
    },
    openModel(isNew, item) {
      const vm = this;
      if (isNew) {
        this.tempData = {}; // 
        this.due_date = '';
        this.isNew = true;  // isNew=true ()
      }else{
        vm.due_date = new Date(item.due_date * 1000).toISOString().split('T')[0];
        this.tempData = Object.assign({},item); // 
        this.isNew = false; // isNew=false ()
      }
      $('#modal-add').modal('show');
    },
    updateData() {
      let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon`;
      let httpMethods = 'post';
      const vm = this;
      
      // api
      if (!vm.isNew) {
        api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon/${vm.tempData.id}`;
        httpMethods = 'put';
      }
      
      this.$validator.validate().then((result) => {
        if(result) {
          this.$http[httpMethods](api, {data : vm.tempData}).then((response) => {
            if (response.data.success) {
              // 
              $('#modal-add').modal('hide');
              vm.getCoupon();
            }else{
              $('#modal-add').modal('hide');
              vm.getCoupon();
              console.log(''+response.data)
            }
          })
        } else {
          console.log('')
          vm.isLoading= false;
        }
      })
      
    },
    deletData(coupon_id) {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon/${coupon_id}`;
      const vm = this;
      const confirmDelet = confirm("?");
      if (confirmDelet == true) {
        this.$http.delete(api).then((response) => {
          vm.getCoupon();
        })
      }      
    },
  },
  created() {
    this.getCoupon();
  },
}
</script>


