<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    
    <button class="btn btn-outline-info" @click="openModel(true)"></button>
    <div class="product-edit top">
      <div class="detail-area">
        <p class="category"></p>
        <p class="title"></p>
        <p class="price"></p>
        <p class="price"></p>
        <p class="price"></p>
        <div class="edit-delet">/</div>
      </div>
      <div class="detail-area" v-for="(item, key) in products" :key="item.id">
        <p class="category">{{ item.category }}</p>
        <p class="title">{{ item.title }}</p>
        <p class="price orng">{{ item.origin_price | currency }}</p>
        <p class="price" v-if="item.price">{{ item.price | currency }}</p>
        <p class="price last" v-else></p>
        <p class="price">
          <b class="onshelves" v-if="item.is_enabled"></b>
          <b class="out_stock" v-else></b>
        </p>
        <div class="edit-delet">
            <button class="btn btn-outline-primary mr-1" @click="openModel(false, item)"></button>
            <button class="btn btn-outline-secondary " @click="deletData(item.id)"></button>
        </div>
      </div>
    </div>
    
    <pagination :page-data="pagination" @changepage="getProducts"></pagination>
    <!-- Modal -->
    <div class="modal fade" id="modal-add" tabindex="-1" role="dialog" aria-hidden="true">
      <div class="modal-dialog modal-dialog-centered modal-lg" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLongTitle"></h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-md-12 mt-2">
                <label></label>
                <input type="text" class="form-control" placeholder="" v-model="tempData.title">
              </div>
              <div class="col-md-6 mt-2">
                <label></label>
                <select class="form-control" v-model="tempData.category">
                  <option value="">--  --</option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                </select>
              </div>
              <div class="col-md-6 mt-2">
                <div class="form-group">
                  <label></label>
                  <input type="text" class="form-control" placeholder="" v-model="tempData.unit">
                </div>
              </div>
              <div class="col-md-6 mt-2">
                <div class="form-group">
                  <label></label>
                  <input type="text" class="form-control" placeholder="" v-model="tempData.origin_price">
                </div>
              </div>
              <div class="col-md-6">
                <div class="form-group">
                  <label></label>
                  <input type="text" class="form-control" placeholder="" v-model="tempData.price">
                </div>
              </div>
              <div class="col-md-12">
                <div class="form-group">
                  <label></label>
                  <textarea rows="5" class="form-control" placeholder="" v-model="tempData.description"></textarea>
                </div>
              </div>
              <div class="col-md-12">
                <div class="form-group">
                  <label></label>
                  <textarea rows="5" class="form-control" placeholder="" v-model="tempData.content"></textarea>
                </div>
              </div>
              <div class="col-md-5 mt-2">
                <img :src="tempData.imageUrl" width="90%" alt="">
              </div>
              <div class="col-md-7 mt-2">
                <div class="form-group">
                  <label></label>
                  <input type="text" class="form-control" placeholder="" v-model="tempData.imageUrl">
                </div>
                <div class="form-group">
                  <label> 
                    <i class="fas fa-spinner fa-spin" v-if="status.fileUploading"></i>
                  </label>
                  <input type="file" class="form-control" @change="uploadImg" ref="files">
                </div>
              </div>
              <div class="col-md-6 mt-3">
                <label>
                  <input type="checkbox" value="0" v-model="tempData.is_enabled"> /
                </label>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal"></button> 
            <button type="button" class="btn btn-primary" @click="updateData"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery';

export default {
  data() {
    return {
      products: [],
      pagination: {},
      tempData: {},
      isNew: false,
      isLoading: false,
      status: {
        fileUploading: false,
      },
    }
  },
  methods: {
    getProducts(num = 1) {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products?page=${num}`;
      const vm = this;
      vm.isLoading= true;
      this.$http.get(api).then((response) => {
        vm.products = response.data.products;
        vm.isLoading= false;
        vm.pagination = response.data.pagination;
        console.log(response)
      })
    },
    openModel(isNew, item) {
      if (isNew) {
        this.tempData = {}; // 
        this.isNew = true;  // isNew=true ()
      }else{
        this.tempData = Object.assign({},item); // 
        this.isNew = false; // isNew=false ()
      }
      $('#modal-add').modal('show');
    },
    updateData() {
      let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product`;
      let httpMethods = 'post';
      const vm = this;
      
      // api
      if (!vm.isNew) {
        api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${vm.tempData.id}`;
        httpMethods = 'put';
      }
      
      this.$http[httpMethods](api, {data : vm.tempData}).then((response) => {
        if (response.data.success) {
          // 
          $('#modal-add').modal('hide');
          vm.getProducts();
        }else{
          $('#modal-add').modal('hide');
          vm.getProducts();
          console.log(''+response.data)
        }
      })
    },
    deletData(product_id) {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${product_id}`;
      const vm = this;
      const confirmDelet = confirm("?");
      if (confirmDelet == true) {
        this.$http.delete(api).then((response) => {
          vm.getProducts();
        })
      }      
    },
    uploadImg() {
      const vm = this;
      const uploadedFile = this.$refs.files.files[0];
      const formdata = new FormData();
      formdata.append('file-to-upload',uploadedFile);
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/upload`;

      vm.status.fileUploading = true; //loading
      this.$http.post(api, formdata, {
        headers: {
          'Content-Type': 'multipart/form-data',
        }
      }).then((response) => {
        vm.status.fileUploading = false;
        if(response.data.success){
          vm.$set(vm.tempData, 'imageUrl', response.data.imageUrl);
        }else{
          //  msg:push
          this.$bus.$emit('msg:push', response.data.message, 'danger');
        }     
      })
    },

  },
  created() {
    this.getProducts();
  },
}
</script>

<style lang="scss" scoped>

</style>


